.creperie_main1 {
    background-color: rgb(193, 229, 229);
    display: flex;
    margin: auto;
    height: 100vh;
}

.creperie_main2 {
    /* background-color: orange; */
    display: flex;
    margin: auto;
    width: 700px;
    height: 560px;
    position: relative;
    background-color: white;

    /* overflow: hidden; */
}



.Csquare1 {
    position: absolute;
    top: 10%;
    left: 0%;


    width: 10%;
    height: 8%;
    border-top: 2px solid black;
    border-bottom: 2px solid black;
}

.Csquare2 {
    position: absolute;
    top: 7%;
    left: 10%;


    width: 50%;
    height: 13%;
    border: 4px solid black;

    display: flex;
    align-items: center;
    justify-content: center;
}

.Ctxt1 {
    /* background-color: blue; */
    font-size: 2.5em;
    font-weight: 800;
}

.Ctxt2 {
    /* background-color: blue; */
    font-size: 1.5em;
    font-weight: 600;
    text-align: center;
    margin-top: 10px;
}


.Csquare3 {
    position: absolute;
    top: 30%;
    left: 25%;

    width: 50%;
    height: 55%;
    border: 1px solid black;
    background-color: rgb(42, 165, 159);

    border-radius: 10px;
}


.Cinput2 {
    position: absolute;
    top: 20%;
    left: 15%;

    width: 70%;
    height: 65%;
    border: 1px solid black;
    background-color: rgba(175, 209, 207, 1);

}





.btn1 {
    position: absolute;
    bottom: 10px;
    left: 35%;

    height: 10%;
    width: 30%;
    background-color: rgb(126, 199, 83);
    border: 2px solid white;
    cursor: pointer;

    font-size: 1.4em;
    font-weight: 600;
}


.btn1:hover {
    border: 2px solid black;
    background-color: rgb(130, 233, 70);
    cursor: pointer;
}




.btn_retour_creperie {
    position: absolute;
    top: 10px;
    right: 10px;
}