.restau_main1 {
    background-color: rgb(193, 229, 229);
    display: flex;
    margin: auto;
    height: 100vh;
}

.restau_main2 {
    /* background-color: orange; */
    background-color: rgb(247, 247, 247);
    display: flex;
    margin: auto;
    width: 700px;
    height: 560px;
    position: relative;

    /* overflow: hidden; */
}


.square1 {
    position: absolute;
    top: 0%;
    left: 40%;


    width: 20%;
    height: 10%;
    border-right: 2px solid black;
    border-left: 2px solid black;
}

.square2 {
    position: absolute;
    top: 10%;
    left: 25%;


    width: 50%;
    height: 10%;
    border: 4px solid black;

    display: flex;
    align-items: center;
    justify-content: center;
}

.txt1 {
    /* background-color: blue; */
    font-size: 2.5em;
    font-weight: 800;
}

.square3bis {
    position: absolute;
    top: 30%;
    left: 30%;


    width: 40%;
    height: 60%;
    /* border: 1px solid black; */

    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-evenly;

    z-index: 2;

    transform: rotate3d(0, 1, 0, 90deg);
    opacity: 0;
    transition: all 4s ease-in;
}
.square3bisv {
    position: absolute;
    top: 30%;
    left: 30%;


    width: 40%;
    height: 60%;
    /* border: 1px solid black; */

    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-evenly;

    z-index: 2;

    /* transform: rotate3d(0, 1, 0, 90deg); */
    opacity: 1;
    transition: all 4s ease-in 2s;
}

.square3 {
    position: absolute;
    top: 30%;
    left: 30%;


    width: 40%;
    height: 60%;
    border: 1px solid black;

    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-evenly;

    background-color: rgb(207, 154, 154);

    border-radius: 5px;

    transform: rotate3d(0, 1, 0, 0deg);
    opacity: 1;
    transition: all 3s ease-in;
}

.square3v {
    position: absolute;
    top: 30%;
    left: 30%;


    width: 40%;
    height: 60%;
    border: 1px solid black;

    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-evenly;

    background-color: rgb(207, 154, 154);

    border-radius: 5px;

    transform: rotate3d(0, 1, 0, 90deg);
    opacity: 0;
    transition: all 4s ease-in;
}


.txt2 {
    /* background-color: blue; */
    font-size: 2em;
    font-weight: 800;
}

.txt3 {
    /* background-color: blue; */
    font-size: 2em;
    font-weight: 600;
}

.input1 {
    height: 10%;
    border: 3px solid transparent;
    margin-bottom: 30px;
}

.input1v {
    height: 10%;
    border: 3px solid green;
    background-color: rgb(146, 230, 146);
    margin-bottom: 30px;
}

.input2 {
    height: 10%;
    border: 3px solid transparent;
    margin-bottom: 30px;
}

.input2v {
    height: 10%;
    border: 3px solid green;
    background-color: rgb(146, 230, 146);
    margin-bottom: 30px;
}

