.heli_main {
    background-color: rgb(193, 229, 229);
    display: flex;
    margin: auto;
    height: 100vh;
}

.heli_main2 {
    /* background-color: orange; */
    display: flex;
    margin: auto;
    width: 700px;
    height: 560px;
    position: relative;

    /* overflow: hidden; */
}

.heli_icc {
    position: absolute;
    bottom: 0;
    left: 20%;

    font-size: 20em;
    margin: auto;
    opacity: 1;
    transform: translate(0, 0);
    /* background-color: red; */
    transition: all 5s ease-in;
}


.heli_icc2 {
    position: absolute;
    bottom: 0;
    left: 50%;

    font-size: 20em;
    margin: auto;
    opacity: 0;
    transform: translate(300px, -600px);
    /* background-color: red; */
    transition: all 7s ease-in;
}

.pilote {
    height: 50%;
    /* width: 50%; */

    position: absolute;
    right: -20%;
    bottom: 0;
}

.heli_input {
    position: absolute;
    top: 14%;
    left: 26%;

    text-align: center;
    height: 25px;
    width: 46%;
}

.btn_retour_heli{
    position: absolute;
    top: 10px;
    right: 10px;
}